<template>
 <div>
    <cms-breadcrumb to="/consult/list" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>    
    <div class="cms-list-layout">
    <!-- 数据列表 -->
     <cms-prompt :collapse='false' style="margin-bottom:15px;">
       <li>可对不合规的信息进行修改、更正</li>
     </cms-prompt>
     <table class="comment-table" v-loading="loading" v-if="!loading">
      <tbody>
        <tr class="comment-th">
          <td style="padding-left:10px;">
           <span>咨询商品</span>
          </td>
          <td style="padding-left:10px;">
            <div>
              <span>咨询内容</span>
            </div>
          </td>
        </tr>
        <tr style="border:1px solid #EBEEF5;" >
          <td class="comment-goods">
            <div class="comment__inner">
              <img class="product-img" :src="$cms.filterUrl(formData.product.coverImage)">
              <div class="product-title">
                <h3 class="comment-title-h">{{formData.product.name}}</h3>
              </div>
            </div>
         </td>
         <td class="comment-item">
           <div class="comment-remark" style="border-top:0;">
              <p style="font-size:14px;margin-bottom:10px;font-weight:700;">{{formData.member.username}}:</p>
              <el-input type="textarea" v-model="formData.content" :rows="5" style="resize:none;" maxlength="255">
              </el-input>
           </div>
           <div class="comment-remark"  v-for="(info,indexs) in formData.assocatedAnswer" :key="indexs">
             <div v-if="formData.assocatedAnswer.length>0" :style="{'border-top':indexs>0?'1px solid #EBEEF5':''}">
               <p style="margin-bottom:5px;">
                 {{info.product.store.storeName}}&nbsp;回复:
                </p>
               <el-input type="textarea" v-model="info.content" :rows="5" maxlength="255"></el-input>
             </div>
             <p v-if="formData.assocatedAnswer.length==0" class="consult-none">暂无商家回复</p>
           </div>
        </td>
        </tr>
      </tbody>
    </table>
    <div style="text-align:right;margin-top:30px;width:49%;">
      <cms-button size="small" :colorPicker='true' @click.stop="update">修改咨询内容</cms-button>
    </div>
  </div>
 </div>
</template>

<script>
import form from '@/mixins/form'
import { consultGet, consultUpdate } from '@/api/marketing/index.js'
export default {
  mixins: [form],
  data() {
    return {
      formData: {},
      loading: false
    }
  },
  methods: {
    getFormData(id) {
      this.loading = true
      consultGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
        } else {
          this.$errorMessage(res.message)
        }
        this.loading = false
      })
    },
    update() {
      const pList = [
        { id: this.formData.id, content: this.formData.content }
      ]
      if (this.formData.assocatedAnswer.length > 0) { // 若有互动回复
        const tempObj = {
          id: '',
          content: ''
        }
        this.formData.assocatedAnswer.forEach(v => {
          tempObj.id = v.id
          tempObj.content = v.content
        })
        pList.push(tempObj)
      }
      let flag = true
      pList.forEach(v => {
        if (v.content === '' || /^\s/.test(v.content)) {
          flag = false
          if (this.formData.assocatedAnswer.length > 0) {
            this.$errorMessage('请输入买家咨询内容及商家回复')
          } else {
            this.$errorMessage('请输入买家咨询内容')
          }
        }
      })
      if (flag) {
        consultUpdate({ pList: pList }).then(res => {
          if (res.code === 200) {
            this.$successMessage('修改成功')
            this.$routerLink('/consult/list', 'list')
          } else {
            this.$errorMessage(res.message)
          }
        })
      }
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>
<style scoped>
.comment-table{
  width: 100%;
  border: 1px solid #EBEEF5;
  padding: 0 10px;
  color: rgb(96, 98, 102);
  border-collapse: collapse;
  font-size: 12px;
}
.comment-th{
  background-color: #f7f7f7;
  text-align: left;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  font-weight: normal;
}
.comment-goods{
  width: 40%;
  padding: 10px 0;
  border-right:1px solid #EBEEF5;
  vertical-align: top;
}
.comment-item{
  width: 60%;
  vertical-align: top;
}
.comment-title{
  font-size: 12px;
  max-width: 360px;
  overflow-wrap: break-word;
}
.comment-remark{
  border-top: 1px solid rgb(235, 238, 245);
  padding-right: 5%;
}
</style>




